x:99;\r\n\r\n}\r\n\r\n#inputSearch {\r\n    width: 100%;\r\n    max-width: 400px;\r\n    margin-right: 3px;\r\n    color: #ccc;\r\n    background-color: rgba(0,0,0,0.6);\r\n    border: 1px solid #ccc;\r\n    font-size: 1.4em;\r\n    text-align: center;\r\n    transition: 150ms;\r\n}\r\n\r\n#inputSearch:hover {\r\n  background-color: rgba(0,0,0,0.6);\r\n  box-shadow: 0 0 5px 5px rgba(255, 255, 0, 0.479);\r\n\r\n}\r\n\r\n#inputSearch:focus {\r\n  border: 1px solid #ccc;\r\n  box-shadow: 0 0 5px 5px rgba(255, 255, 0, 0.479);\r\n  outline: none;\r\n}\r\n\r\n#submitSearch {\r\n  margin-left: 3px;\r\n  color: #333;\r\n  background-color: rgb(255, 217, 0);\r\n  border: 1px solid #ccc;\r\n  font-size: 1.4em;\r\n  text-align: center;\r\n}\r\n\r\n#submitSearch:hover {\r\n  color: #ccc;\r\n  background-color: rgba(0,0,0,0.6);\r\n  box-shadow: 0 0 5px 5px rgba(255, 255, 0, 0.479);\r\n\r\n}\r\n\r\n#submitSearch:focus {\r\n  border: 1px solid #ccc;\r\n  outline: none;\r\n}\r\n\r\n/* RESPONSIVE CSS\r\n  -------------------------------------------------- */\r\n\r\n@media (min-width: 40em) {\r\n    /* Bump up size of carousel content */\r\n    .carousel-caption p {\r\n      margin-bottom: 1.25rem;\r\n      font-size: 1.25rem;\r\n      line-height: 1.4;\r\n    }\r\n  \r\n    .featurette-heading {\r\n      font-size: 50px;\r\n    }\r\n  }\r\n\r\n@media (min-width: 62em) {\r\n    .featurette-heading {\r\n      margin-top: 7rem;\r\n    }\r\n  }\r\n/*# sourceMappingURL=data:application/json;base64, */"